import { useContext } from "react";
import { FaStar } from "react-icons/fa";

const Star = ({ selected }) => (
	<FaStar style={{ color: selected ? "red" : "white" }}></FaStar>
);

const rating = ({ numberOfStars = 5 }) => {
	const [selected, setSelected] = useContext(false);
	return [...Array(numberOfStars)].map((x, i) => (
		<Star onClick={() => setSelected(!selected)} selected={selected} />
	));
};

export default rating;
